import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { AccountService, FileUploadService, UtilsService } from '../../../_services';
import { environment } from '../../../../environments/environment';
import { first } from 'rxjs/operators';

import { createAvatar } from '@dicebear/avatars';
import * as styleAvatar from '@dicebear/pixel-art';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['../account.component.less'],
  encapsulation: ViewEncapsulation.None,
})
export class ProfileComponent implements OnInit {
    username = '';
    user = null;
    apiurl = environment.apiUrl;
    tab = 'tab1';
    constructor(
      private route: ActivatedRoute,
      private AccountService: AccountService,
      private FileUploadService: FileUploadService,
      private UtilsService: UtilsService
    ) { }

    ngOnInit() {
      this.username = this.route.snapshot.params['username'];
      this.AccountService.getByUsername(this.username)
        .pipe(first())
        .subscribe(user => {
          this.user = user;

          this.user.register = this.UtilsService.format_date(this.user.register);
          this.user.birth_date = this.user.show_birth_date == '0' ? 'Caché' : this.user.birth_date;
          this.user.location = this.user.location == null ? 'Aucun' : this.user.location;
          this.user.url = this.user.url == null ? 'Aucun' : this.user.url;
          this.user.signature = this.user.signature == null ? 'Aucun' : this.user.signature;

          this.user.topics[0].subject = this.UtilsService.TextSub(this.user.topics[0].subject, 37);
          this.user.topics[0].posted = this.UtilsService.format_date(this.user.topics[0].posted);
          this.user.posts[0].topics.subject = this.UtilsService.TextSub(this.user.posts[0].topics.subject, 37);
          this.user.posts[0].posted = this.UtilsService.format_date(this.user.posts[0].posted);

          this.FileUploadService.getAvatar(this.user.id)
            .pipe(first())
            .subscribe(avatar => {
              const svgAvatar = createAvatar(styleAvatar, {
                  seed: this.user.username,
                  backgroundColor: '#868889',
              });
              if(avatar != '') {
                this.user['avatar'] = this.apiurl+avatar;
              } else {
                this.user['avatar'] = '';
                this.user['svg'] = svgAvatar;
              }
            });

          this.FileUploadService.getCover(this.user.id)
            .pipe(first())
            .subscribe(cover => {
              if(cover != '') {
                this.user['cover'] = this.apiurl+cover;
              } else {
                this.user['cover'] = '';
              }
            });
        });
    }

    openTab(tab) {
      this.tab = tab;
    }
}