import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AccountService, FileUploadService, UtilsService } from '../../../../../_services';
import { User } from '../../../../../_models';

import { environment } from '../../../../../../environments/environment';
import { first } from 'rxjs/operators';

import { createAvatar } from '@dicebear/avatars';
import * as styleAvatar from '@dicebear/pixel-art';



@Component({
  selector: 'app-settingAccount',
  templateUrl: './account.setting.component.html',
})
export class SettingAccountComponent implements OnInit {
  public href: string = "";
  user: User;
  email = '';
  apiurl = environment.apiUrl;
  
  constructor(
    private router: Router,
    private AccountService: AccountService,
    private FileUploadService: FileUploadService,
    private UtilsService: UtilsService
  ) {
    this.AccountService.user.subscribe(x => this.user = x); 
  }

  ngOnInit(): void {
    this.href = this.router.url;

    this.email = this.UtilsService.hideEmail(this.user.email);
  }
}
