import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AccountService, FileUploadService } from '../../../_services';
import { environment } from '../../../../environments/environment';
import { first } from 'rxjs/operators';

import { createAvatar } from '@dicebear/avatars';
import * as styleAvatar from '@dicebear/pixel-art';

@Component({
  selector: 'app-user-list',
  templateUrl: './list.component.html',
  styleUrls: ['../account.component.less'],
  encapsulation: ViewEncapsulation.None,
})
export class UserListComponent implements OnInit {
    users = null;
    constructor(
      private AccountService: AccountService,
      private FileUploadService: FileUploadService,
    ) { }

    ngOnInit() {
      this.AccountService.getAll()
        .pipe(first())
        .subscribe(users => { 
          this.users = users;
          for(let i = 0; i < this.users.length; i++) {
            this.FileUploadService.getAvatar(this.users[i].id)
              .pipe(first())
              .subscribe(avatar => {
                const svgAvatar = createAvatar(styleAvatar, {
                    seed: this.users[i].username,
                    backgroundColor: '#868889',
                });
                if(avatar != '') {
                  this.users[i]['avatar'] = environment.apiUrl+avatar;
                } else {
                  this.users[i]['avatar'] = '';
                  this.users[i]['svg'] = svgAvatar;
                }
              });
          }
        });
    }
}