import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProfileComponent } from './profile/profile.component';
import { CharacterComponent } from './character/character.component';
import { UserListComponent } from './user_list/list.component';

import { SettingComponent } from './setting/setting.component';
import { SettingAccountComponent } from './setting/sections/account/account.setting.component';


const routes: Routes = [
    {
        path: 'users', component: UserListComponent
    },
    {
        path: 'user/:username', component: ProfileComponent
    },
    {
        path: 'character/1', component: CharacterComponent
    },
    {
        path: 'settings', component: SettingComponent,
            children: [
                { path: 'account', component: SettingAccountComponent },
            ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AccountRoutingModule { }